/* FONTS */
* {
  margin: 0;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-family: "Outfit-Light";
  font-size: 30px;
  color: #1F314F;
  line-height: 1.5;
  cursor: url("src/icons/cursor/cursor.svg") 0 0, auto !important;
}

*::-webkit-scrollbar {
  display: none;
}